import React,{Component} from 'react'
import arrow_left from "./arrow-left.jpg";
import arrow_right from "./arrow-right.png";
import arrow_right2 from './arrow_right2.jpg'
import arrow_left2 from './arrow_left2.png'

class Shop extends Component{
    constructor(){
        super()
        this.onClickForward=this.onClickForward.bind(this)
        this.onClickBack = this.onClickBack.bind(this)
        const img1 = require('./Shop-images/pic1.png');
        const img2 = require('./Shop-images/pic2.png');
        const img3 = require('./Shop-images/pic3.png');
        const img4 = require('./Shop-images/pic4.png');
        const img5 = require('./Shop-images/pic5.png');
        const img6 = require('./Shop-images/pic6.png');
        const img7 = require('./Shop-images/pic7.png');
        const img8 = require('./Shop-images/pic8.png');
        const img9 = require('./Shop-images/pic9.png');
        const img10 = require('./Shop-images/pic10.png');
        const img11 = require('./Shop-images/pic11.png');
        const img12 = require('./Shop-images/pic12.png');
        const img13 = require('./Shop-images/pic13.png');
        const img14 = require('./Shop-images/pic14.png');
        const img15 = require('./Shop-images/pic15.png');
        const img16 = require('./Shop-images/pic16.png');

        this.state={
            index:0,
            imgList: [img1,img2,img3,img4,img5,img6,img7,img8,img9,img10,img11,img12,img13,img14,img15,img16],
        }
    }
    onClickForward(){
        if (this.state.index + 1===this.state.imgList.length){
            this.setState({
                index:0
            })
        } else{
            this.setState({
                index: this.state.index +1
            })
        }
    }
    onClickBack(){
        if (this.state.index -1 === -1){
            this.setState({
                index: this.state.imgList.length -1
            })
        } else{
            this.setState({
                index: this.state.index -1
            })
        }
    }
    render() {
        return (
            <div className="container">
                <h4>SHOP</h4>

                    <img src={this.state.imgList[this.state.index]} className="shop-image" alt="" /> <br />
                <img src={arrow_left2} onClick={this.onClickBack} alt=" " className="arrow"/>
                <span>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp; &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</span>
                <img src={arrow_right2} onClick={this.onClickForward} alt=" " className="arrow"/>
            </div>
        )
    }

}
export default Shop;